//canvas setup
export function main() {
    const canvas = document.getElementById('canvas1');
    const ctx = canvas.getContext("2d");    //allow using canvas built in methods
    canvas.width = 600;
    canvas.height = 400;

    let spacePressed = false;
    let angle = 0;
    let hue = 0;
    let frame = 0;
    let score = 0;
    let gamespeed = 2;  //paralax?

    const gradient = ctx.createLinearGradient(0, 0, 0, 70);
    gradient.addColorStop('0.40', '#fff');
    gradient.addColorStop('0.50', '#000');
    gradient.addColorStop('0.55', '#4040ff');
    gradient.addColorStop('0.60', '#000');
    gradient.addColorStop('0.90', '#fff');

    const background = new Image();
    background.src = 'BG.png';
    //Poner la misma imgane una detras de la otra para que cuando una acabe aparezca la otra
    const BG = {
        x1: 0,
        x2: canvas.width,
        y: 0,
        width: canvas.width,
        height: canvas.height
    }

    function handleBackground() {
        if(BG.x1 <= -BG.width + gamespeed) BG.x1 = BG.width;
        else BG.x1 -= gamespeed;
        if (BG.x2 <= -BG.width + gamespeed) BG.x2 = BG.width;
        else(BG.x2 -=gamespeed);
        //we have 2 identical images, the second starts where the first finishes
        ctx.drawImage(background, BG.x1, BG.y, BG.width, BG.height);
        ctx.drawImage(background, BG.x2, BG.y, BG.width, BG.height);
    }

    function animate() {
        ctx.clearRect(0,0, canvas.width, canvas.height);
        handleBackground();
        //ctx.fillRect(10,temp,50,50);
        handleObstacles();

        bird.update();
        bird.draw();
        ctx.fillStyle = gradient;
        ctx.font="90px Georgia";
        ctx.strokeText(score, 450, 70);
        ctx.fillText(score, 450, 70);
        handleParticle();
        if (handleCollisions() ) return;
        requestAnimationFrame(animate);
        angle+=0.12;
        hue++;
        frame++;    //framecount
    }

    animate();

    window.addEventListener('keydown', function(e) {
        //console.log(e.code)   //name of key pressed
        if (e.code === 'Space') spacePressed = true;
    });

    window.addEventListener('keyup', function(e) {
        if (e.code === 'Space') spacePressed = false;
        bird.frameX = 0;
    });

    const bang = new Image();
    bang.src = 'bang.png';
    function handleCollisions() {
        for ( let i = 0; i < obstaclesArray.length; i++) {
            if ( bird.x < obstaclesArray[i].x + obstaclesArray[i].width &&
                bird.x + bird.width > obstaclesArray[i].x &&
                //top collision
                ((bird.y < 0 + obstaclesArray[i].top && bird.y + bird.height > 0) ||
                //bottom collision
                (bird.y > canvas.height - obstaclesArray[i].bottom && 
                    bird.y + bird.height < canvas.height))) {
                        //collision detected
                        ctx.drawImage(bang, bird.x, bird.y, 50, 50);
                        ctx.font = "25px Georgia";
                        ctx.fillStyle = 'white';
                        ctx.fillText('Game Over, your score is ' + score, 160, canvas.height/2 - 10);
                        return true;
                    }
        }
        return false;
    }
}